import { Component, OnInit } from '@angular/core';
import * as echarts from 'echarts';
import { VisualData } from '../../shared/visualdata.service';
import { namevalueis } from '../../bean/namevalue';
import { RESULT_OK } from '../../bean/RegisterOrLogin';

@Component({
  selector: 'app-job-seeker-chart',
  templateUrl: './job-seeker-chart.component.html',
  styleUrls: ['../position-chart/position-chart.component.css']
})
export class JobSeekerChartComponent implements OnInit {

  /* 顶部 图表 */
  option3: any;
  /* 底部 图表 */
  option: any;
  /* 底部 默认显示图表 用户学历&工作经验 */
  eUserEdu: any;
  eUserExper: any;

  /* 切换 两个各个按钮 */
  flag = 0;
  constructor(
    private vd: VisualData
  ) {
    /* 默认展示 */
    this.defaultShowEcharts()
  }

  ngOnInit() { }

  changeOption(num: number) {
    /* 默认显示一张图表 */
    this.flag = 1;
    switch (num) {
      /* 用户学历（圆环图）&工作经验（饼图） */
      case 0:
        this.flag = 0;
        this.vd.getUserEduExper().subscribe(
          data => {

            if (data['statu'] == RESULT_OK.YES) {

              let userEdu: namevalueis[];
              let userExper: namevalueis[];
              userEdu = data['data']['chart'].useredu;
              userExper = data['data']['chart'].userexper;
              this.eUserExper = {
                title: {
                  text: '用户经验',
                  subtext: '根据用户信息分析所得',
                  left: 'center',
                  textStyle: {
                    color: "#ddd"
                  }
                },
                tooltip: {
                  trigger: 'item',
                  formatter: "{b}用户 : {c}<br/>约占({d}%)"
                },
                series: [{
                  name: '',
                  type: 'pie',
                  radius: [0, 120],
                  center: ['50%', '50%'],
                  clockwise: false,
                  data: userEdu,
                  label: {
                    normal: {
                      show: true,
                      textStyle: {
                        color: '#999',
                        fontSize: 14,
                      }
                    }
                  },
                  itemStyle: {
                    emphasis: {
                      borderWidth: 0,
                      shadowBlur: 10,
                      shadowOffsetX: 0,
                      shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                  }
                }],
                color: ['#ad46f3', '#44aff0', '#f6d54a', '#f69846', '#6f81da', '#ad46f3'],

              };
              this.eUserEdu = {
                title: {
                  text: '用户教育经历',
                  subtext: '根据用户信息分析所得',
                  left: 'center',
                  textStyle: {
                    color: "#ddd"
                  }
                },
                tooltip: {
                  trigger: 'item',
                  formatter: "{b}用户 : {c}<br/>约占({d}%)"
                },
                series: [{
                  name: '',
                  type: 'pie',
                  radius: [100, 120],
                  center: ['50%', '50%'],
                  clockwise: false,
                  data: userExper,
                  label: {
                    normal: {
                      show: true,
                      textStyle: {
                        color: '#999',
                        fontSize: 14,
                      }
                    }
                  },
                  itemStyle: {
                    emphasis: {
                      borderWidth: 0,
                      shadowBlur: 10,
                      shadowOffsetX: 0,
                      shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                  }
                }],
                color: ['#ad46f3', '#44aff0', '#f6d54a', '#f69846', '#6f81da', '#ad46f3'],
              };
            }
          }
        )
        break;
      /* 用户偏爱岗位(折线图) */
      case 1:
        this.vd.getUserFavJob().subscribe(
          data => {
            if (data['statu'] == RESULT_OK.YES) {
              let iJob = data['data']['chart'].favJob;
              /* 最受求职者欢迎的岗位 x 岗位 y 数量 */
              let xJob: string[] = [];
              let yJobNum: number[] = [];
              for (let k = 0; k < iJob.length; k++) {
                xJob.push(iJob[k].name);
                yJobNum.push(iJob[k].value);
              }
              this.option = {
                // backgroundColor: '#293042',
                tooltip: {
                  trigger: 'item',
                  formatter: "喜爱{b}人数: {c}"
                },
                title: {
                  text: "求职者喜爱岗位",
                  subtext: "根据搜索分析所得",
                  x: 'center',
                  top: 10,
                  textStyle: {
                    color: "#fff"
                  }
                },
                grid: {
                  left: '4%',
                  top: '25%',
                  bottom: '1%',
                  containLabel: true
                },
                xAxis: {
                  type: 'category',
                  boundaryGap: true,
                  axisTick: {
                    show: false
                  },
                  axisLine: {
                    lineStyle: {
                      color: '#6995aa'
                    },
                    onZero: true
                  },
                  axisLabel: {
                    fontSize: 11,
                    margin: 10,
                    color: '#eee',
                    interval: 0,
                    rotate: 25
                  },
                  splitLine: {
                    show: false
                  },
                  data: xJob
                },
                yAxis: {
                  type: 'value',
                  name: "喜爱人数 /",
                  axisTick: {
                    show: false
                  },
                  axisLine: {
                    lineStyle: {
                      color: '#6995aa'
                    },
                    onZero: false
                  },
                  axisLabel: {
                    fontSize: 11,
                    color: '#eee'
                  },
                  splitLine: {
                    lineStyle: {
                      type: 'dotted',
                      color: '#1978D9'
                    }
                  },
                  scale: true
                },
                series: [{
                  name: '',
                  smooth: true,
                  type: 'line',
                  symbolSize: 5,
                  symbol: 'circle',
                  itemStyle: {
                    normal: {
                      label: {
                        formatter: function (params) {
                          return 100 - params.value;
                        },
                        fontSize: 40,
                        padding: [90, 0, 0, 0],
                        color: '#fff',
                        textStyle: {
                          baseline: 'top'
                        }
                      },
                      color: '#ffea00',
                      borderColor: 'rgba(255, 234, 0, 0.5)',
                      borderWidth: 10
                    }
                  },
                  data: yJobNum
                }]
              };
            }
          }
        );
        break;
      /* 用户喜爱城市(饼图) */
      case 2:
        this.vd.getUserFavCity().subscribe(
          data => {
            if (data['statu'] == RESULT_OK.YES) {
              let userFavCity = data['data']['chart'].favCity;
              this.option = {
                // backgroundColor: "#eee",
                title: {
                  text: '求职者喜爱城市',
                  subtext: '根据搜索分析所得',
                  left: 'center',
                  top: 20,
                  textStyle: {
                    color: '#fff'
                  },
                },
                color: ['#ad46f3', '#44aff0', '#f6d54a', '#f69846', '#6f81da', '#ad46f3'],
                tooltip: {
                  trigger: 'item',
                  formatter: "喜爱{b}人数: {c}<br/>占比约({d}%)"
                },
                calculable: true,
                series: [
                  {
                    name: 'tt',
                    type: 'pie',
                    radius: '55%',
                    center: ['50%', '60%'],
                    data: userFavCity,
                  }
                ]
              }
            }
          }
        )
        break;
      /* 用户 性别分布(环状图) TODO: 性别数据接口*/
      case 3:
        this.vd.getUserSex().subscribe(
          data => {
            if (data['statu'] == RESULT_OK.YES) {
              var echartData = data['data']['chart'].usersex;
              var rich = {
                yellow: {
                  color: "#ffc72b",
                  fontSize: 30,
                  padding: [5, 4],
                  align: 'center'
                },
                total: {
                  color: "#ffc72b",
                  fontSize: 40,
                  align: 'center'
                },
                white: {
                  color: "#fff",
                  align: 'center',
                  fontSize: 14,
                  padding: [21, 0]
                },
                blue: {
                  color: '#49dff0',
                  fontSize: 16,
                  align: 'center'
                },
                hr: {
                  borderColor: '#0b5263',
                  width: '100%',
                  borderWidth: 1,
                  height: 0,
                }
              }
              this.option = {
                backgroundColor: '#2d3548',
                title: {
                  text: '性别分布',
                  subtext: "根据用户信息分析所得",
                  left: 'center',
                  padding: [24, 0],
                  textStyle: {
                    color: '#fff',
                    fontSize: 18,
                    align: 'center'
                  }
                },
                legend: {
                  selectedMode: false,
                  formatter: function (name) {
                    var total = 0; //各科正确率总和
                    var averagePercent; //综合正确率
                    echartData.forEach(function (value, index, array) {
                      total += value.value;
                    });
                    return "总数 \n" + '{total|' + total + '}';
                  },
                  data: [echartData[0].name],
                  // data: ['高等教育学'],
                  // itemGap: 50,
                  left: 'center',
                  top: 'center',
                  icon: 'none',
                  align: 'center',
                  textStyle: {
                    color: "#fff",
                    fontSize: 16,
                    rich: rich
                  },
                },
                series: [{
                  name: '总数',
                  type: 'pie',
                  radius: ['42%', '50%'],
                  hoverAnimation: false,
                  color: ['#c487ee', '#deb140', '#49dff0', '#034079', '#6f81da', '#00ffb4'],
                  label: {
                    normal: {
                      formatter: function (params, ticket, callback) {
                        var total = 0; //考生总数量
                        var percent; //考生占比
                        echartData.forEach(function (value, index, array) {
                          total += value.value;
                        });
                        percent = ((params.value / total) * 100).toFixed(1);
                        return '{white|' + params.name + '}\n{hr|}\n{yellow|' + params.value + '}\n{blue|' + percent + '%}';
                      },
                      rich: rich
                    },
                  },
                  labelLine: {
                    normal: {
                      length: 55,
                      length2: 0,
                      lineStyle: {
                        color: '#0b5263'
                      }
                    }
                  },
                  data: echartData
                }]
              };
            }
          }
        )
        break;
    }
  }


  defaultShowEcharts() {
    /* 底部 eUserEdu eUserExper 两个图表  */
    this.changeOption(0);
    /* 顶部 用户年龄分布（折线图）*/
    this.vd.getUserAge().subscribe(
      data => {
        if (data['statu'] == RESULT_OK.YES) {
          /* x年龄 y年龄阶段对应数量 */
          let xAge: string[] = [];
          let yNum: number[] = [];
          const temp = data['data']['chart'].userage;
          for (let i = 0; i < temp.length; i++) {
            xAge.push(temp[i].name);
            yNum.push(temp[i].value);
          }

          this.option3 = {
            // backgroundColor: "#2d3548",
            tooltip: {
              trigger: 'axis',
              formatter: '{b}' + '岁' + '<br/> {a}' + '共: ' + '{c}' + '位',
              axisPointer: {
                lineStyle: {
                  color: '#57617B'
                }
              }
            },
            /* legend: {
              data: ['求职者'],
              right: '4%',
              textStyle: {
                fontSize: 12,
                color: '#F1F1F3'
              }
            }, */
            grid: {
              left: '3%',
              right: '5%',
              bottom: '3%',
              containLabel: true
            },
            xAxis: [{
              type: 'category',
              name: '/ 岁',
              nameTextStyle: {
                color: "#aaa"
              },
              boundaryGap: false,
              axisTick: {
                show: false
              },
              axisLine: {
                show: false
              },
              axisLabel: {
                textStyle: {
                  color: '#aaa'
                }
              },
              data: xAge,
            }],
            yAxis: [{
              type: 'value',
              name: '人数 /',
              nameTextStyle: {
                color: "#aaa"
              },
              axisTick: {
                show: false
              },
              axisLine: {
                show: false
              },
              axisLabel: {
                margin: 10,
                textStyle: {
                  color: '#aaa'
                }
              },
              splitLine: {
                lineStyle: {
                  color: '#2d3549'
                }
              }
            }],
            series: [{
              name: '求职者',
              type: 'line',
              smooth: true,
              symbol: 'circle',
              symbolSize: 5,
              showSymbol: false,
              lineStyle: {
                normal: {
                  width: 1
                }
              },
              areaStyle: {
                normal: {
                  color: {
                    "type": "linear",
                    "x": 0,
                    "y": 0,
                    "x2": 0,
                    "y2": 1,
                    "colorStops": [
                      {
                        offset: 0,
                        color: 'rgba(0, 136, 212, 0.3)'
                      },
                      {
                        offset: 0.8,
                        color: 'rgba(0, 136, 212, 0)'
                      }
                    ]
                  },
                  shadowColor: 'rgba(0, 0, 0, 0.1)',
                  shadowBlur: 10
                }
              },
              itemStyle: {
                normal: {
                  color: 'rgb(0,136,212)',
                  borderColor: 'rgba(0,136,212,0.2)',
                  borderWidth: 12

                }
              },
              data: yNum,
            },]
          };
        }
      }
    )
  }

}



